<template>
  <div class="hybridcom">
    <el-form :label-position="labelPosition" :model="form" status-icon :rules="rules" ref="form" label-width="170px" class="demo-form">
      <!--choose sdf-->
      <el-form-item  label="Upload SDF" >
        <UploadCom :isSDF="true"
                   tipmsg="Upload SDF file to provide Source Sequence to decode (One Line Per Sequence)" @func="getSDF"
                   v-if="hackReset == true"
        ></UploadCom>
      </el-form-item>

      <!--data processing-->
      <!--HBD-->
      <el-form-item label="HBD less than" prop="HBD">
        <el-tooltip placement="top">
          <div slot="content">Number of Hydrogen Bond Donors less than
            <br>
            Default: 8</div>
          <el-input v-model.number="form.HBD"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--HBA-->
      <el-form-item label="HBA less than" prop="HBA">
        <el-tooltip placement="top">
          <div slot="content">Number of Hydrogen Bond Acceptors less than
            <br>
            Default: 8</div>
          <el-input v-model.number="form.HBA"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--MW-->
      <el-form-item label="MW less than" prop="MW">
        <el-tooltip placement="top">
          <div slot="content">The average molecular weight of the molecule less than
            <br>
            Default: 800</div>
          <el-input v-model.number="form.MW"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--rings-->
      <el-form-item label="rings greater than" prop="rings">
        <el-tooltip placement="top">
          <div slot="content">Symmetrized SSSR for a molecule greater than
            <br>
            Default: 1</div>
          <el-input v-model.number="form.rings"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--Linker length-->
      <el-form-item label="Linker_len greater than" prop="len">
        <el-tooltip placement="top">
          <div slot="content">Linker's shortest path Default: 1</div>
          <el-input v-model.number="form.len"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--HEV-->
      <el-form-item label="HEV greater than" prop="HEV">
        <el-tooltip placement="top">
          <div slot="content">GetNumHeavyAtoms Default: 2</div>
          <el-input v-model.number="form.HEV"></el-input>
        </el-tooltip>
      </el-form-item>

      <!-- Whether to use own model-->
      <el-form-item label-width="200px" label="Use your own model" prop="ifOwnModel">
        <el-tooltip placement="top">
          <div slot="content">Whether to use your own model sampling</div>
          <el-switch
              v-model="form.ifOwnModel"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="Yes"
              inactive-text="No">
          </el-switch>
        </el-tooltip>
      </el-form-item>
      <!--choose model-->
      <el-form-item  label="Upload Model" v-if="form.ifOwnModel" >
        <UploadCom :isSDF="false"
                   tipmsg="Upload your own  model for sampling" @func="getModel"
                   v-if="hackReset == true"
        ></UploadCom>
      </el-form-item>
      <!--Using the existing model-->
      <el-form-item v-show="!form.ifOwnModel" label="Other Models" prop="model_rule">
        <el-tooltip placement="top">
          <div slot="content">Select the model provided by the system</div>
          <el-select v-model="form.selected_model" >
            <el-option
                v-for="item in models"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-tooltip>
      </el-form-item>
      <!--batch size-->
      <el-form-item label="batch size" prop="batch_size">
        <el-tooltip placement="top">
          <div slot="content">Batch size
            Default: 64</div>
          <el-input v-model.number="form.batch_size"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--max length-->
      <el-form-item label="max length" prop="max_length">
        <el-tooltip placement="top">
          <div slot="content">Maximum prediction length.
            Default: 200</div>
          <el-input v-model.number="form.max_length"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--beam size-->
      <el-form-item label="beam size" prop="beam_size">
        <el-tooltip placement="top">
          <div slot="content">Beam size
            Default: 10</div>
          <el-input-number v-model="form.beam_size" controls-position="right"  :min="10" :max="30"></el-input-number>
        </el-tooltip>
      </el-form-item>
      <!--beam size-->
<!--      <el-form-item label="beam size" prop="beam_size">-->
<!--        <el-radio v-model="form.beam_size" :label=10 border>10</el-radio>-->
<!--        <el-radio v-model="form.beam_size" :label=30 border>30</el-radio>-->
<!--      </el-form-item>-->
      <!--n_best-->
      <el-form-item label="n_best" prop="n_best">
        <el-tooltip placement="top">
          <div slot="content">n_best
            Default: 10</div>
          <el-input-number v-model="form.n_best" controls-position="right"  :min="10" :max="300"></el-input-number>
        </el-tooltip>
      </el-form-item>
      <!--email-->
      <el-form-item label="Email" prop="email">
        <el-input v-model="form.email" placeholder="Please enter your email address"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">submit</el-button>
        <el-button @click="resetForm('form')">reset</el-button>
      </el-form-item>
    </el-form>
    <el-card shadow="always" v-show="ifrun">
      <h2>The child process is {{pid}}</h2>
    </el-card>
  </div>
</template>

<script>
import UploadCom from "@/components/UploadCom";
import {getListen} from "@/api/Get";
export default {
  name: "HybridCom",
  components: {
    UploadCom
  },
  data() {
    var check = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Could not be empty'));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Please enter a numeric value'));
        } else {
          callback();
        }
      }, 500);
    };
    return {
      labelPosition: 'left',
      pid:'',
      ifrun:false,//ifrun==true,show pid
      models: [
        { label: 'ChEMBL', value: 'ChEMBL_model_average.pt' },
        { label: 'ZINC', value: 'zinc_3000w.pt' }
      ],
      hackReset: true,
      form: {
        HBD:8,
        HBA:8,
        MW: 800,
        rings: 1,
        sdfPath:'',
        modelPath:'',
        ifOwnModel:true,//if true use Own Model
        selected_model: 'ChEMBL_model_average.pt',
        len:1,
        HEV:2,
        batch_size:64,
        max_length:200,
        beam_size:10,
        n_best:10,
        email:''
      },
      rules: {
        HBD:[
          { validator: check, trigger: 'blur' }
        ],
        HBA:[
          { validator: check, trigger: 'blur' }
        ],
        MW:[
          { validator: check, trigger: 'blur' }
        ],
        rings:[
          { validator: check, trigger: 'blur' }
        ],
        batch_size:[
          { validator: check, trigger: 'blur' }
        ],
        max_length:[
          { validator: check, trigger: 'blur' }
        ],
        email:[
          { required: true, message: 'Please enter your email address', trigger: 'blur' },
          { type: 'email', message: 'Please enter the correct email address', trigger: ['blur', 'change'] }
        ]
      }
    };
  },
  props: {
    url: String,
  },
  methods: {
    getSDF(data){
      this.form.sdfPath=data
      // console.log('sdf:   '+this.form.sdfPath);
    },
    getModel(data){
      this.form.modelPath=data
      // console.log('model:   '+this.form.modelPath);
    },

    submitForm(formName) {
      var checkS = this.checkSubmit()
      this.$refs[formName].validate((valid) => {
        if (valid && checkS) {
          // 发送请求
          getListen(this.url,this.form).then(res=>{
            // console.log(res);
            this.pid = res.pid+3
            this.ifrun = true
            this.$notify({
              title: 'Training is running',
              message: 'The child process is '+this.pid,
              type: 'success'
            });
          }).catch(err=>{
            // console.log(err);
            this.$alert(err, 'Run failed', {
              confirmButtonText: 'confirm',})
          })
        }else{
          this.$alert('Input information is incomplete or incorrect','Submit failed', {
            confirmButtonText: 'confirm',
            type: 'warning'})
          return false;
        }
        // } else {
        //   // console.log('error submit!!');
        //   this.$message({
        //     showClose: true,
        //     message: 'Input information is incomplete or incorrect',
        //     type: 'error'
        //   });
        //   return false;
        // }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true;
      });
    },
    checkSubmit(){
      if(this.form.sdfPath==''){
        return false;
      }else {
        if(this.form.ifOwnModel==true
            && this.form.modelPath=='')
          return false;
        else{
          return true;
        }
      }
    }
  },
  updated () {
    if(this.form.n_best<this.form.beam_size){
      this.form.beam_size=10
    }
  }
}
</script>

<style scoped>
.el-button{
  width:120px;
  height:40px;
  font-size:15px
}
.el-input{
  width:410px;
}
.el-card {
  width: 48%;
}
.upload-demo {
  width:500px;
}
</style>
